// web/pages/articles/[id].js
import Link from 'next/link';

export default function Article({ article }) {
    if (!article) {
        return (
            <div style={{ maxWidth: 800, margin: '0 auto', padding: 16 }}>
                <p>文章未找到或正在加载…</p>
                <Link href="/">返回列表</Link>
            </div>
        );
    }

    return (
        <div style={{ maxWidth: 800, margin: '0 auto', padding: 16 }}>
            <Link href="/">← 返回</Link>
            <h1>{article.title}</h1>
            <div dangerouslySetInnerHTML={{ __html: article.content }} />
            <p><small>阅读量: {article.view_count || 0} • 创建: {article.created_at}</small></p>
        </div>
    );
}

export async function getServerSideProps(ctx) {
    const { id } = ctx.params;
    try {
        const res = await fetch(`http://localhost:3000/api/v1/articles/${id}`);
        if (!res.ok) return { props: { article: null } };
        const json = await res.json();
        return { props: { article: json } };
    } catch (err) {
        return { props: { article: null } };
    }
}
